import React from 'react';

const defaultHeight = 'auto'

export default function Icon(props) {
  const iconImage = require('../../assets/images/icons/' + props.image + '.svg');

  return <img className='icon' src={ iconImage } alt={ props.image } style={ { height: props.height || defaultHeight } } />
}




